import React, { useState, useEffect } from 'react';
import { Form, Button, Container, Row, Col, ListGroup } from 'react-bootstrap';
import './TrainingRoutine.css'; // Importa el archivo CSS

const exerciseImages = {
  Sentadillas: '/images/sentadillas.png',
  Dominadas: '/images/dominadas.png',
  Flexiones: '/images/flexiones.png',
  Abdominales: '/images/abdominales.gif',
  Correr: '/images/correr.png',
};

const TrainingConfigurator = () => {
  const [day, setDay] = useState('Lunes');
  const [exercise, setExercise] = useState('');
  const [reps, setReps] = useState('');
  const [series, setSeries] = useState('');
  const [routine, setRoutine] = useState(() => {
    const savedRoutine = localStorage.getItem('routine');
    return savedRoutine ? JSON.parse(savedRoutine) : [];
  });

  useEffect(() => {
    localStorage.setItem('routine', JSON.stringify(routine));
  }, [routine]);

  const handleAddExercise = (e) => {
    e.preventDefault();
    if (exercise && reps && series) {
      setRoutine([...routine, { day, exercise, reps, series }]);
      setExercise('');
      setReps('');
      setSeries('');
    }
  };

  const handleDeleteExercise = (index) => {
    const newRoutine = routine.filter((_, i) => i !== index);
    setRoutine(newRoutine);
  };

  return (
    <Container>
      <h2>Configurador de Rutina</h2>
      <Form onSubmit={handleAddExercise}>
        <Form.Group as={Row} controlId="formDay">
          <Form.Label column sm={2}>Día de la semana:</Form.Label>
          <Col sm={10}>
            <Form.Control as="select" value={day} onChange={(e) => setDay(e.target.value)}>
              <option value="Lunes">Lunes</option>
              <option value="Martes">Martes</option>
              <option value="Miércoles">Miércoles</option>
              <option value="Jueves">Jueves</option>
              <option value="Viernes">Viernes</option>
              <option value="Sábado">Sábado</option>
              <option value="Domingo">Domingo</option>
            </Form.Control>
          </Col>
        </Form.Group>
        <Form.Group as={Row} controlId="formExercise">
          <Form.Label column sm={2}>Ejercicio:</Form.Label>
          <Col sm={10}>
            <Form.Control as="select" value={exercise} onChange={(e) => setExercise(e.target.value)}>
              <option value="">Selecciona un ejercicio</option>
              <option value="Sentadillas">Sentadillas</option>
              <option value="Dominadas">Dominadas</option>
              <option value="Flexiones">Flexiones</option>
              <option value="Abdominales">Abdominales</option>
              <option value="Correr">Correr</option>
            </Form.Control>
          </Col>
        </Form.Group>
        <Form.Group as={Row} controlId="formReps">
          <Form.Label column sm={2}>Repeticiones:</Form.Label>
          <Col sm={10}>
            <Form.Control
              type="number"
              value={reps}
              onChange={(e) => setReps(e.target.value)}
            />
          </Col>
        </Form.Group>
        <Form.Group as={Row} controlId="formSeries">
          <Form.Label column sm={2}>Num. Series:</Form.Label>
          <Col sm={10}>
            <Form.Control
              type="number"
              value={series}
              onChange={(e) => setSeries(e.target.value)}
            />
          </Col>
        </Form.Group>
        <Button variant="primary" type="submit" className="mt-3">Agregar Ejercicio</Button>
      </Form>
      <div className="mt-4">
        <h3>Configuración Actual para {day}</h3>
        <ListGroup>
          {routine
            .filter((item) => item.day === day)
            .map((item, index) => (
              <ListGroup.Item key={index} className="d-flex justify-content-between align-items-center">
                {item.exercise} - {item.series} series de {item.reps} repeticiones
                <Button variant="danger" size="sm" onClick={() => handleDeleteExercise(index)}>Eliminar</Button>
              </ListGroup.Item>
            ))}
        </ListGroup>
      </div>
    </Container>
  );
};

export default TrainingConfigurator;