import React from 'react';
import { Container, Table } from 'react-bootstrap';
import './Schedule.css'; // Importa el archivo CSS

const Schedule = () => {
    const hours = [
        "8:30 - 9:15",
        "9:30 - 10:15",
        "---",
        "17:30 - 18:15",
        "18:30 - 19:15",
        "19:30 - 20:15",
        "20:30 - 21:15",
      ];

  const exampleData = [
    ["MANTENIMIENTO","AEROSTEP","BODY PAM","AEROFITBALL","ESTIRAMIENTOS"],
    ["AEROFITBALL","GLUTEBOOM","CICLO INDOOR","BODY PAM","BODY BALANCE"],
    ["---","---","---","---","---"],
    ["CARDIO HIT","BODY PAM","CICLO INDOOR","E.F.E (CIRCUITO)","BODY BALANCE"],
    ["BODY PAM","BODY ATTACK","TONO FUNCIONAL","AEROJUMP","LATINOS"],
    ["STEP XTREME","TONO FUNCIONAL","T.B.C","BODY COMBAT","CICLO INDOOR"],
    ["CORE & STRECH","CICLO INDOOR","GLUTEBOOM","BODY PAM","---"]
  ];

  return (
    <Container className="mt-5">
      <h2>Horario</h2>
      <Table bordered hover responsive className="schedule-table">
        <thead>
          <tr className="table-header">
            <th>Hora</th>
            <th>Lunes</th>
            <th>Martes</th>
            <th>Miércoles</th>
            <th>Jueves</th>
            <th>Viernes</th>
          </tr>
        </thead>
        <tbody>
        {exampleData.map((row, rowIndex) => (
            <tr key={rowIndex}>
              <td>{hours[rowIndex]}</td>
              {row.map((activity, colIndex) => (
                <td key={colIndex}>{activity}</td>
              ))}
            </tr>
          ))}
        </tbody>
      </Table>
    </Container>
  );
};

export default Schedule;